.header {
  background: var(--secundary);
  grid-area: header;
  height: 3.4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
}

.header a {
  height: 100%;
}

.logo {
  height: 100%;
  object-fit: contain;
  filter: drop-shadow(0 .2rem .2rem rgba(0, 0, 0, .8));
}

.navBarSearch {
  width: 12rem;
  display: flex;
  justify-content: space-between;
  padding: 0 .5rem;
}

.iconsHeader {
  width: 8rem;
  display: flex;
  justify-content: space-between;
}

.IconsNav {
  margin: 0 .4rem;
  cursor: pointer;
  filter: drop-shadow(0 .08rem .09rem rgba(70, 70, 70, .8));
}

.IconsNav:hover {
  opacity: .8;
}

@media ( max-width: 360px ) {
  .navBarSearch {
    width: 9.8rem;
    padding: 0;
  }
}