.footer {
  background: var(--secundary);
  grid-area: footer;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
}

.locationInfo {
  font-size: .9rem;
  display: flex;
  gap: .5rem;
}

.dataFooter {
  cursor: pointer;
}

@media ( max-width: 360px ) {
  .footer {
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 0 .3rem;
  }
}