.searchForm {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.inputSearch {
  width: 75%;
  background: transparent;
  border: none;
  outline: none;
  font-size: .9rem;
}

.fa-search {
  color: #444444;
  font-size: 1rem;
  margin: 0 .12rem;
}

.labelSearch {
  background: #eeeeee;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  box-shadow: 
  0 0 .2rem var(--shadow),
  inset 0 0 .1rem #2C2C2C;
  position: absolute;
  cursor: pointer;
}

.labelSearch:hover {
  filter: drop-shadow(0 0 .01rem #000);
}

.fa-times {
  color: #444444;
  position: absolute;
  left: .8rem;
  cursor: pointer;
}

.btnSearch {
  margin: 0 .6rem;
  border-radius: .4rem;
  padding: .12rem;
  cursor: pointer;
  font-weight: bold;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 4px 4px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.btnSearch:active {
  box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px 0px inset;
  opacity: .8;
}