.toggle {
  display: inline-block;
}

.checkbox {
  /* opacity: 0; */
  display: none;
  position: absolute;
}

.checkbox:checked + .labelToggle .ballLabelToggle {
  transform: translateX(1.1rem);
}

.labelToggle {
  background: var(--colorSwitch);
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 3rem;
  width: 2rem;
  height: .9rem;
  padding: 0 .2rem;
  margin: 0 .4rem;
  cursor: pointer;
  position: relative;
  box-shadow: 0 0 .2rem rgba(0, 0, 0, .6);
}

.labelToggle i {
  font-size: .8rem;
}

.fa-sun {
  color: #cbae00;
}

.ballLabelToggle {
  background: var(--colorBallSwitch);
  position: absolute;
  left: 0;
  width: .9rem;
  height: .9rem;
  border-radius: 50%;
  transition: transform .2s linear;
  box-shadow: 
    0 0 .1rem .01rem rgba(0, 0, 0, .8),
    inset 0 0 .5rem -.02rem rgba(111, 111, 111, 0.2);
}